.font12{
    @include fontSize(12);
}

.font13{
    @include fontSize(13);
}

.font14{
    @include fontSize(14);
    
    @include maxWidth(576){
        @include fontSize(12);
    }
}
.font16{
    @include fontSize(16);

    @include maxWidth(576){
        @include fontSize(14);
    }
}